.appWrapper {
    display: flex;
    flex-direction: column;
    font-size: 14px;
    height: auto;
    overflow: auto;
    border: 1px solid rgb(160, 161, 161);
}
.offsetWind {
    margin: 3px 0 3px 0;
}
.offsetExpl {
    margin: 3px 0 2px 4px;
}
.indent3per {
    margin-left: 3%;
}
.indent6per {
    margin-left: 6%;
}
.indent9per {
    margin-left: 9%;
}
.header {
    display: flex;
}
.main {
    display: flex;
    padding: 1%;
    height: 100%;
    height: 400px;
}
.explorer {
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(160, 161, 161);
    flex: 1 100px;
}
.explorer__header {
    text-align: center;
    border-bottom: 1px solid rgb(160, 161, 161);
}
.explorer__row {
    display: flex;
    align-items: center;
}
.explorer__column {
    display: flex;
    flex-direction: column;
}
.row__sub {
    left: 5%;
    position: relative;
}
.row__files {
    left: 2%;
    position: relative;
    white-space: nowrap;
}
.row__subFile {
    left: 12%;
    left: 10%;
    position: relative;
}
.row__10sub {
    left: 10px;
    position: relative;
}
.header__window {
    border-bottom: 1px solid rgb(160, 161, 161);
    text-align: center;
}
.window {
    border: 1px solid rgb(160, 161, 161);
    border-left: 0px solid rgb(160, 161, 161);
    flex: 7 300px;
    /* white-space: pre-wrap; */
    overflow: hidden;
}
.windowContent {
    white-space: pre-wrap;
    /* height: 100%; */
    height: 94%;
    overflow-y: auto;
    user-select: text;
    position: relative;
}
.marker {
    display: flex;
}
.cursorPointer {
    cursor: pointer;
}
.folder::before {
    font-family: eicons;
    position: relative;
    left: 1%;
    color:#d7d999;
    content: '\e91f ';
}

.folderBracket::before {
    font-family: eicons;
    position: relative;
    left: 1%;
    color:rgb(199, 211, 211);
    content: '\e89e';
    transform: scale(1.4) ;
}

.rotate0 {
    /* transform: rotate(0deg); */
    animation: rotate0 300ms ease-in-out forwards;
}

.rotate90 {
    /* display: block; */
    animation: rotate90 300ms ease-in-out forwards;
}

@keyframes rotate90 {
    0% {
        transform: rotate(0);
    }
    100% {
       transform: rotate(90deg);
    }
}

@keyframes rotate0 {
    0% {
        transform: rotate(90deg);
    }
    100% {
       transform: rotate(0);
    }
}

.fileJs::before {
    position: relative;
    color:#d7d999;
    font-size: 12px;
    font-weight: 700;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    content: 'JS';
    margin-right: 5px;
}

.fileTS::before {
    position: relative;
    color:#4496e1;
    font-size: 11px;
    font-weight: 700;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    content: 'TS';
    margin-right: 5px;
}

.fileTSEvnD::before {
    position: relative;
    color:#4496e1;
    font-size: 11px;
    font-weight: 700;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    content: 'TS';
    margin-right: 5px;
}
.fileCss {
    position: relative;
    left: 16px;
}
.fileCss::before {
    position: relative;
    background-image: url(./assets/img/css3-svgrepo-com.svg);
    display: flex;
    background-size: 14px;
    width: 14px;
    height: 14px;
    background-repeat: no-repeat;
    position: absolute;
    left: -16px;
    top: 1px;
    content: '';
}

/* ПОЛОСА ПРОКРУТКИ */

.container {
    overflow-x: auto;
    height: 100%;
}
 /* фон самого скроллбара */
.container::-webkit-scrollbar,
.windowContent::-webkit-scrollbar {
     height: 7px;
     width: 10px;
}
/* индикатор прокрутки, перетаскиваемый элемен */
.container::-webkit-scrollbar-thumb,
.windowContent::-webkit-scrollbar-thumb {
     border-radius: 10px; /* закругления плашки R не меньше ½ ширины*/
     height: 20px;
     background-color: #3d3d3d; /* цвет плашки */
     border: 1px solid #9e9e9e; /* padding вокруг плашки цвет скрола */
}
  /* пустое пространство под индикатором прокрутки. */
  .container::-webkit-scrollbar-track,
  .windowContent::-webkit-scrollbar-track {
     background-color: #9e9e9e;
  }
  .container__sub {
    width: 89%;
  }
  .navbarToggle{
    display: none;
}

@media  (max-width: 400px) {

    .vabar {
        display: flex;
        z-index: 20;
        position: relative;
    }
    .navbarToggle{
        position: relative;
        float: right;
        padding: 9px 10px;
        background-color: transparent;
        background-image: none;
        border: 1px solid transparent;
        border-radius: 4px;
        display: block;
    }
    .rsMenuForm__burger span:nth-child(1) {
        transform: rotate(0deg) translate(0);
        transition: 0.2s;
    }

    .navbarToggle .iconBar {
        display: block;
        background-color: #dbdb1c;
        width: 22px;
        height: 2px;
        border-radius: 1px;
    }
    .navbarToggle .iconBar + .iconBar {
        margin-top: 4px;
    }
}


.rsMenuForm__burger span:nth-child(1) {
    transform: rotate(0deg) translate(0);
    transition: 0.2s;
}
.rsMenuForm__burger span:nth-child(2) {
    opacity: 1;
    transform: rotate(0deg) translate(0);
    transition: 0.2s;
}
.rsMenuForm__burger span:nth-child(3) {
    transform: rotate(0deg) translate(0);
    transition: 0.2s;
}

.rsMenuForm__burger.active span:nth-child(1) {
    /* transform: rotate(45deg) translate(6px,5.5px);
    transition: 0.2s; */
    animation: active1 200ms ease-in-out forwards;
}
@keyframes active1 {
    0% {
        transform: rotate(0deg) translate(0);
    }
    100% {
        transform: rotate(45deg) translate(6px,5.5px);
    }
}
.rsMenuForm__burger.active span:nth-child(2) {
    /* opacity: 0;
    transition: 0.2s;
    transition: transform 0.6s ease 1s; */
    animation: active2 200ms ease-in-out forwards;
}
@keyframes active2 {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.rsMenuForm__burger.active span:nth-child(3) {
    /* transform: rotate(-45deg) translate(3.5px,-2.5px);
    transition: 0.2s; */
    animation: active3 200ms ease-in-out forwards;
}
@keyframes active3 {
    0% {
        transform: rotate(0deg) translate(0);
    }
    100% {
        transform: rotate(-45deg) translate(3.5px,-2.5px);
    }
}


.rsMenuForm__burger.notActive span:nth-child(1) {
    /* transform: rotate(45deg) translate(6px,5.5px);
    transition: 0.2s; */
    /* transform: rotate(45deg) translate(6px,5.5px); */
    animation: notActive1 200ms ease-in-out forwards;
}
@keyframes notActive1 {
    0% {
        transform: rotate(45deg) translate(6px,5.5px);
    }
    100% {
        transform: rotate(0deg) translate(0);
    }
}
.rsMenuForm__burger.notActive span:nth-child(2) {
    /* opacity: 0;
    transition: 0.2s;
    transition: transform 0.6s ease 1s; */
    animation: notActive2 200ms ease-in-out forwards;
}
@keyframes notActive2 {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.rsMenuForm__burger.notActive span:nth-child(3) {
    animation: notActive3 200ms ease-in-out forwards;
}
@keyframes notActive3 {
    0% {
        transform: rotate(-45deg) translate(3.5px,-2.5px);
    }
    100% {
        transform: rotate(0deg) translate(0);
    }
}



@keyframes fadeInLeft {
    from {
        opacity: 0;
        transform: translate3d(-100%, 0, 0)
    }
    to {
        opacity: 1;
        transform: none
    }
}

.fadeInLeft {
    animation: 1s 1 fadeInLeft;
}


@keyframes fadeOutLeft {
    from {
        opacity: 1
    }

    to {
        opacity: 0;
        transform: translate3d(-100%, 0, 0)
    }
}

.fadeOutLeft {
    animation: 1s 1 fadeOutLeft;
}


