.containerHiaderButton {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 1rem 0;
    position: relative;
}

.iconClipbord, .iconCheck {
    font-size: 12px;
    color:#d7d999;
    border: 1px solid;
    width: 100px;
    margin: 0 auto;
    background-color: transparent;
    border: none;
    border-radius: var(--button-radius);
    color: #c57ba4;
    margin: 0;
    text-transform: uppercase;
}
.iconClipbord::before {
    font-family: eicons;
    position: relative;
    left: -5px;
    color:#d7d999;
    /* color:#e91008; */
    content: '\e919 ';

}
.iconCheck::before {
    font-family: eicons;
    position: relative;
    left: -5px;
    color:#d7d999;
    /* color:#e91008; */
    content: '\e90e ';
}

/* Для пакета SyntaxHighlighter корректирую шрифт*/
.containerPositionAbsolute {
    position: absolute;

}
.containerSyntaxhigHlighter {
    margin-bottom: 1em;
}
.containerSyntaxhigHlighter pre {
    line-height: 0 !important;
    padding: 0 !important ;
    margin: 0 0.5em  !important ;
    overflow: hidden !important;;
}
.containerSyntaxhigHlighter pre code {
    line-height: 0 !important;
    padding: 0 10px !important;
    margin: 0  !important ;
    white-space: pre-wrap;
    user-select: text;
    overflow: hidden !important;
}
.containerSyntaxhigHlighter code span {
    font-size: 13px;
    line-height: 1.4 !important;
    font-family: "Fira Code", "Fira Mono", Menlo, Consolas, "DejaVu Sans Mono", monospace;
    margin-top: -10px;
    user-select: text;
}
/* END- Для пакета SyntaxHighlighter корректирую шрифт*/


@media  (max-width: 400px) {

    .container { /* ПОЛОСА ПРОКРУТКИ */
        overflow-x: auto;
        height: 100%;
        max-height: calc(100% - 18px);
    }
    /* .explorer {
        display: none;
        position: absolute;
        background-color: black;
        background-color: #282c34;
        width: 86%;
        height: 100%;
        z-index: 10;
    } */

    .explorerShow {
        display: block;
        position: absolute;
        background-color: black;
        background-color: #282c34;
        width: 86%;
        height: 90%;
        max-height: 400px;
        z-index: 10;
        animation: explorerShow 200ms ease-in-out forwards;
    }
    @keyframes explorerShow {
        0% {
            transform: translate3d(-120%, 0, 0);
        }
        100% {
            transform: translate3d(0, 0, 0);
        }
    }
    .explorerHide {
        opacity: 1;
        position: absolute;
        background-color: black;
        width: 86%;
        height: 90%;
        max-height: 400px;
        animation: explorerHide 200ms ease-in-out forwards;
    }

    @keyframes explorerHide {
        0% {
            opacity: .6;
            transform: translate3d(0, 0, 0);
        }
        100% {
            opacity: 0;
            transform: translate3d(-120%, 0, 0);
        }
    }
    .window {
        border: 1px solid rgb(160, 161, 161);
        /* border-left: 0px solid rgb(160, 161, 161); */
        flex: 7 300px;
    }

}
