.strusture {
    max-width: 878px;
    width: 100%;
    position: relative;
}
ul.list {
    margin-bottom: 16px;
    width: 100%;
}

ul.list {
    list-style-type: none;
    padding-left: 34px;
}

ul.list>li:before {
    display: inline-block;
    position: absolute;
    left: 0;
    content: "\2014";
}
ul.list>li {
    margin: 1% 0;
}
.strusture__title > * {
    font-size: 24px;
}
.subItem {
    padding-left: 3%;
}
ul.list>li.subItem:before {
    display: inline-block;
    position: absolute;
    left: 4%;
    content: '\25CF';
}
.subSubItem {
    padding-left: 6%;
}
ul.list>li.subSubItem:before {
    display: inline-block;
    position: absolute;
    left: 6%;
    content: "\2013";
}