@import url('https://fonts.googleapis.com/css2?family=Concert+One&display=swap');

.app-wrapper {
    display: block;
    border: 1px solid red;
    margin: 0 auto;
    font-size: 14px;

    max-width: 800px;
    max-height: 600px;
    overflow: hidden;
    font-family: 'Concert One', cursive;
}
.main {
    display: flex;
    flex-direction: column;
    font-size: 1.2em;
}
.main .title {
    margin: 0 2% 2%;
    font-family: 'Concert One', cursive;
}
.main .title h1 {
    font-size: 1.03em;
    font-family: 'Concert One', cursive;
}

.main-intro {
    display: flex;
    justify-content: space-between;
    flex: 30%;
    padding: 1% 2%;
    max-height: 100%;
}
.main-intro__logo {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 128px;
}
.main-intro__logo>.logo {
    max-width: 128px;
    max-height: 128px;
    width: auto;
}
.main-intro__text {
    flex-grow: 2.5;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1% 2%;
}

.main-intro__text h2 {
    font-size: 1.5rem;
    margin: 0;
}
.main-question {
    padding: 2%;
}
.main-question__quantity {
    font-family: 'Concert One', cursive;
    font-size: .93em;
    margin-bottom: 1%;
}
.main-question__quantity span {
    font-family: 'Concert One', cursive;
    font-size: .93em;
    margin-bottom: 1%;
}
.main-intro__description span {
    font-size: .93em;
}
.circle {
    display: flex;
    gap: 1%;
}
.circle__target {
    border-radius: 50%;
    width: 24px;
    height: 24px;
    background-color: white;
    border: 1px solid black;
}
.circle__target:nth-child(1) {
    border-radius: 50%;
    width: 24px;
    height: 24px;
    background-color: #00B28E;
    border: 1px solid black;
}
.circle__target:nth-child(2) {
    border-radius: 50%;
    width: 24px;
    height: 24px;
    background-color: #00B28E;
    border: 1px solid black;
}
.circle__target:nth-child(3),
.circle__target:nth-child(4),
.circle__target:nth-child(5) {
    border-radius: 50%;
    width: 24px;
    height: 24px;
    background-color: red;
    border: 1px solid black;

}
.circle-gadget {
    display: none;
}
.circle-gadget__target:nth-child(1) {
    display: flex;
    justify-content: center;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    background-color: #00B28E;
    border: 1px solid black;
}
.circle-gadget__target:nth-child(2) {
    display: flex;
    justify-content: center;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    background-color: red;
    border: 1px solid black;
}
.main-question__text {
    padding-top: 2%;
}
.main-question__text p {
    margin: 0;
}
.answers {
    padding: 2%;
}
ul.answers li {
    list-style-type: none;
}
ul.answers li label {
    list-style-type: none;
    font-family: 'Concert One', cursive;
    font-size: 14px;
    margin-left: 5px;
    padding: 0;
}
ul.answers li label,
ul.answers li input {
    cursor: pointer;
}
.button.next-question {
    border: none;
    background: #00B28E;
    display: block;
    padding: 8px 16px;
    overflow: hidden;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    color: #fff;
    font-size: 1.1em;
    margin: 20px auto;
    width: 200px;
    border-radius: 6px;
}
@media (max-width: 800px) {
    .app-wrapper {
        overflow: scroll;
    }

    .title {
        font-size: calc(var(--index) * 1.2);
    }

    .main-intro {
        flex-direction: column
    }

    .main-intro__logo>.logo {
        width: calc(var(--index) * 8);
    }

    .main-intro__text {
        font-size: calc(var(--index) * 1.2);
    }

    .circle__target {
        display: none;
    }

    .circle-gadget {
        display: flex;
        justify-content: space-around;
    }
}
@media (max-width: 800px) and (min-aspect-ratio: 128/128) {
    .app-wrapper {
        overflow: scroll;
    }

    .title {
        font-size: calc(var(--index) * 1.2);
    }

    .main-intro {
        flex-direction: column
    }

    .main-intro__text {
        font-size: calc(var(--index) * 1.2);
    }

    .circle__target {
        display: none;
    }

    .circle-gadget {
        display: flex;
        justify-content: space-around;
    }
}
@media (max-width: 400px) {
    .main-intro__text {
        font-size: 12px;
    }

}
@media (max-width: 400px) and (min-aspect-ratio: 300/128) {
    .main-intro__text {
        font-size: 12px;
    }
}
@media (max-height: 495px) {
    .button.next-question {
        margin: 2.5vh auto;
    }
}