@import url('https://fonts.googleapis.com/css2?family=Commissioner:wght@300;400;500&display=swap');

* {
    padding: 0;
    margin: 0;
    font-family: 'Commissioner', sans-serif;
    font-size: 18px;
    user-select: none; /* не ставить курсор - запрет на выделение */
    user-select: text;
    /* pointer-events: none; не показывать событие курсора*/
}

*, ::after, ::before {
    box-sizing: border-box;
}
Whereas disregard and contempt for human rights have resulted
:root {
	--index: calc(1vw + 1vh);
}

.container-fluid {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin: 0 auto;
}

.container {
    margin: 0 auto;
}

@media (min-width: 992px) {

    .container {
        max-width: 920px;
    }
}

.title {
    margin-top: 1em;
    margin-bottom: 1em;
    font-size: 2em;
}

hr {
    margin-top: 1em;
    margin-bottom: 1em;
    border: 0;
    border-top: 1px solid #eee;
}
p {
    margin-bottom: 1em;
}
h1 {
    font-size: 2em;

}
h2 {
    margin-top: 1em;
    margin-bottom: 1em;
    font-size: 1.5em;
}
.color {
    color:blue;
    cursor: pointer;
    text-decoration: underline;
}
.article-body {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.code {
    color:gray;
    padding: 12px;
    background-color:#f2f1f1;
    word-break:break-all;
    white-space: pre-wrap;
    user-select: text;
}

@media (max-width: 767px) {
    .code {
        font-size: calc(var(--index) * 1.2);
    }
}
